/* ARROW */
@-webkit-keyframes bouncing {
    0% {bottom: 0;}
    50% {bottom: 20px;}
    100% {bottom: 0;}
}
@keyframes bouncing {
    0% {bottom: 0;}
    50% {bottom: 20px;}
    100% {bottom: 0;}
}

.arrow {
    -webkit-animation: bouncing 1s infinite ease-in-out;
            animation: bouncing 1s infinite ease-in-out;
    background-image: url(/static/media/arrow.f1fd5aae.ico);
    -webkit-filter: invert(100%) sepia(0%) saturate(839%) hue-rotate(203deg) brightness(113%) contrast(100%);
            filter: invert(100%) sepia(0%) saturate(839%) hue-rotate(203deg) brightness(113%) contrast(100%);
    /* RETURNS FILTERS FOR CHANGE COLOR OF PNG IF GIVEN HEXCODE 
    https://codepen.io/sosuke/pen/Pjoqqp */
    background-size: contain;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left:-20px;
    width: 40px;
    height: 40px;
}
