* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
#customNav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    font-size: 18px;
    padding-top: 1px;
    background-color: rgb(57, 76, 76);
    padding-bottom: 10px;
}
/* dropdown menu */
.main-nav {
    list-style-type: none;
    display: block;
}

/* hide drop down menu */
.main-nav-gone {
    display: none;
}
.nav-links, #logo {
    text-decoration: none;
    color:  rgba(255,255,255,0.8);    
}

.main-nav li {
    text-align: center;
    margin: 15px auto;
}

#logo {
    display: inline-block;
    font-size: 22px;
    margin-top: 10px;
    margin-left: 20px;
}

.navbar-toggle {
    position: absolute;
    top: 10px;
    right: 20px;
    cursor: pointer; 
    color: rgba(255,255,255,0.8);
    font-size: 24px;
}

.nav-links:hover, #logo:hover{
    text-decoration: none;
    color: white;
    cursor: pointer;
}
/* LARGE SCREEN */
@media screen and (min-width: 768px) {
    #customNav {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        padding-bottom: 0;
        -webkit-align-items: center;
                align-items: center;
        background-color: transparent;
    }
    .main-nav li {
        margin: 0;
        margin-right:30px;
    }
    /* main-nav and main-nav-gone */
    #js-menu {
        display: -webkit-flex;
        display: flex;
        list-style-type: none;
        margin-right: 30px;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: flex-end;
                justify-content: flex-end;
        margin-top: 10px;
    }
    .nav-links {
        margin-left: 40px;
        color:rgba(42, 42, 42, 0.877)
    }

    #logo {
        margin-top: 0;
        color:rgba(42, 42, 42, 0.877)
    }

    .navbar-toggle {
        display: none;
    }

    .nav-links:hover, #logo:hover{
        color: black;
    }
}
